
<template>
  <div>
    <h3>الأجهزة المرتبطة بالحساب</h3>

    <div class="card mb-3 p-3" v-if="!device">
      <form @submit.prevent="create">
        <div class="text-center">
          <button class="btn btn-primary" :disabled="creating">
            {{ creating ? "جار الإنشاء" : "إضافة جهاز جديد" }}
          </button>
        </div>
      </form>
    </div>

    <div class="card p-3 mb-3" v-if="device">
      <ul class="list-group" v-if="!loading">
        <div class="card">
          <div class="card-header bg-primary border-card">
            <h4 class="text-white text-center">
              الجهاز الرئيسى <br />
              الحالة: {{ device.status == "ready" ? "متصل" : "غير متصل" }}
            </h4>
          </div>

          <div class="card-footer bg-primary text-center">
            <div>
              <button
                class="btn btn-secondary me-2"
                @click="fetchQr(device.instanceId)"
              >
                عرض الـQR
              </button>
              <button
                class="btn btn-success"
                @click="openSend(device.instanceId)"
              >
              تجربة الجهاز
              </button>

              <button
                class="btn btn-danger m-2"
                @click="logout(device.instanceId)"
              >
                تسجيل خروج من الجهاز
              </button>
            </div>
          </div>
        </div>
      </ul>
    </div>

    <div v-if="loading" class="loader-box">
      <div class="loader-15"></div>
    </div>

    <!-- QR Display -->
    <div v-if="qrBase" class="card p-3 mb-3 text-center">
      <h6>
        QR (image) - الرجاء الربط فى خلال 30 ثانيه - بعد ذلك الرجاء طلب QR جديد
      </h6>
      <h6>
        يجب أن يكون الجهاز غير مرتبط من قبل
      </h6>

      <img
        :src="qrBase"
        alt="QR"
        style="width: 400px; height: 400px; object-fit: contain"
      />
    </div>
    <div class="col-4-md col-lg-4 col-sm-12" v-if="subscription.plan_type == 'open'">
      <div class="mb-3">
        <label for="webhookUrl" class="form-label"
          >رابط الـ Webhook (HTTPS فقط)</label
        >
        <input
          v-model="webhookUrl"
          type="text"
          id="webhookUrl"
          @click="saveWebhook()"
          class="form-control text-start"
          placeholder="https://example.com/webhook"
        />
      </div>
      <button
        class="btn btn-primary w-100"
        :disabled="loading"
        @click="saveWebhook"
      >
        {{ loading ? "جارٍ الحفظ..." : "حفظ الرابط" }}
      </button>
      <div v-if="message" class="alert alert-info  mt-3" >
        {{ message }}
      </div>
    </div>

    <div v-if="showSend" class="card p-3 col-4-md col-lg-4 col-sm-12">
      <h6>تجربة الجهاز</h6>
      <form @submit.prevent="sendMessage">
        <div class="mb-2">
          <label class="form-label">الرقم  - كود الدوله مطلوب   </label>
          <input
            v-model="to"
            class="form-control"
            placeholder="+201012345678"
            required
          />
        </div>
        <div class="mb-2">
          <label class="form-label">الرسالة</label>
          <textarea v-model="msg" class="form-control" required></textarea>
        </div>
        <button class="btn btn-primary">إرسال</button>
        <button class="btn btn-link" @click.prevent="closeSend">إلغاء</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref, onMounted } from "vue";
import QRCode from "qrcode";
import Subscribe from "./Subscribe.vue";

export default {
  setup() {
    const loading = ref(false);
    const creating = ref(false);
    const subscription = ref([]);

    const qrBase = ref(null);
    const showSend = ref(false);
    const activeInstance = ref(null);
    const to = ref("");
    const msg = ref("تجربة من الجهاز");
    const device = ref([]);
    const webhookUrl = ref("");
    const message = ref("");
    async function load() {
      loading.value = true;
      try { 
        const r = await axios.get("/api/instances");
        device.value = r.data.device ;
        subscription.value = r.data.subscription || r.data;
      } catch (err) {
        console.error(err);
      } finally {
        loading.value = false;
      }
    }
    async function logout(instanceId) {
      try {
        await axios.post(`/api/instances/${instanceId}/logout`);
        location.reload();
      } catch (err) {
        alert(err.response?.data?.error || err.message);
      }
    }

    async function create() {
      creating.value = true;
      try {
        await axios.post("/api/instances");
        await load();
      } catch (err) {
        alert(err.response?.data?.error || err.message);
      } finally {
        creating.value = false;
      }
    }

    async function fetchQr(instanceId) {
      try {
        loading.value = true;
        const r = await axios.get(`/api/instances/${instanceId}/qr`);
        if (r.data && r.data.qr) {
          qrBase.value = await QRCode.toDataURL(r.data.qr);
          loading.value = false;
        } else {
          qrBase.value = null;
          alert("No QR available");
        }
      } catch (err) {
        alert(err.response?.data?.error || err.message);
        loading.value = false;
      }
    }
    const saveWebhook = async () => {
      message.value = "";

      // ✅ Validate URL
      if (!webhookUrl.value.startsWith("https://")) {
        message.value = "يجب أن يبدأ الرابط بـ https://";
        return;
      }

      loading.value = true;

      try {
        const res = await axios.post("/api/subscriptions/set_webhook", {
          webhookUrl: webhookUrl.value.trim(),
        });
        if (res.data.success || res.status === 200) {
          message.value = "✅ تم حفظ رابط الـ Webhook بنجاح";
        } else {
          message.value = "حدث خطأ أثناء الحفظ";
        }
      } catch (err) {
        message.value =
          err.response?.data?.message || "حدث خطأ أثناء الاتصال بالخادم";
      } finally {
        loading.value = false;
      }
    };

    function openSend(instanceId) {
      activeInstance.value = instanceId;
      showSend.value = true;
    }
    function closeSend() {
      showSend.value = false;
      activeInstance.value = null;
    }

    async function sendMessage() {
      try {
        await axios.post("/api/instances/send", {
          to: to.value,
          body: msg.value,
        });
        alert("Sent");
        closeSend();
      } catch (err) {
        alert(err.response?.data?.error || err.message);
      }
    }

    onMounted(load);

    return {
      loading,
      create,
      creating,
      fetchQr,
      qrBase,
      showSend,
      openSend,
      closeSend,
      activeInstance,
      to,
      msg,
      logout,
      sendMessage,
      device,
      webhookUrl,
      message , 
      saveWebhook , 
      subscription
    };
  },
};
</script>